.prompt {
  font-size: 30px;
  font-family: 'Neucha', cursive;
  margin-left: 10px;
}

.question {
  border: 1px solid black;
  text-align: center;
  margin-top: 20px;
  width: 50%;
  margin-left: 25%;
}

.answer-boxes {
  margin-bottom: 10px;
  margin-left: 10px;
  width: 60%;
}


@media only screen and (max-width: 768px){
  .question {
    margin-left: auto;
    width: auto;
  }

  .hiddenForPC {
    display: inline;
  }
}